import React, { Fragment, useEffect, useState } from 'react';
import { Button, CustomInput, Card, CardBody, CardColumns, CardTitle, CardText, Nav, NavItem, UncontrolledTooltip } from 'reactstrap';

import classNames from 'classnames';
import fetch from "cross-fetch";
import FalconCardHeader from '../components/common/FalconCardHeader';
import { ReactFuri } from 'react-furi';


const Flashcard = (props) => {
    const {index, color, showFuri, flip, data} = props
    const [toggle, setToggle] = useState(false);
    const [furiToggle, setFuriToggle] = useState(false);

    const [sideAOrig, setSideAOrig] = useState(<></>);
    const [sideBOrig, setSideBOrig] = useState(false);

    const [sideA, setSideA] = useState(false);
    const [sideB, setSideB] = useState("");


    function init(inShowFlip, inShowFuri) {

        let key = index + "_a";
        let id = "w" + index;
        //let aComp = <ReactFuri key={key} word={data["orthBase"]} reading={data["hiraBase"]} showFuri={inShowFuri} />;
        let aComp = (<><ReactFuri id={id} key={key} word={data["orthBase"]} reading={data["hiraBase"]} showFuri={inShowFuri} />
        <UncontrolledTooltip placement="top" target={id}>{data["hiraBase"]}</UncontrolledTooltip></>);

        let bStr = "";
        if (data["pos1"] === data["cType"]) {
            bStr = data["pos1"];
        } else {
            if (data["cType"] !== "*") {
                bStr = data["pos1"] + data["cType"];
            } else {
                bStr = data["pos1"];
            }
        }

        let sideBStr = bStr;
        if ("side_b" in data) {
            sideBStr = buildSideB(data["side_b"], bStr);
        }
        let bComp = (<Fragment>{sideBStr}</Fragment>);

        setSideAOrig(aComp)
        setSideBOrig(bComp);

        if (inShowFlip === true) {
          setSideA(bComp);
          if (toggle === true) {
              setSideB(aComp);
          } else {
              setSideB(false);
          }
        } else {
          setSideA(aComp);
          if (toggle === true) {
              setSideB(bComp);
          } else {
              setSideB(false);
          }
        }
    }

    useEffect(() => {

        init(flip, showFuri);
        setFuriToggle(showFuri);

    }, [showFuri]);

    useEffect(() => {

        init(flip, furiToggle);

    }, [flip, furiToggle]);


    function show(shouldShow) {
      if (flip === true) {
          setSideA(sideBOrig);
          if (shouldShow === true) {
              setSideB(sideAOrig);
          } else {
              setSideB(<></>);
          }
      } else {
          setSideA(sideAOrig);
          if (shouldShow === true) {
              setSideB(sideBOrig);
          } else {
              setSideB(<></>);
          }
      }
    }

    function buildSideB(str, wordType) {
        str = str.replace(/\"/i, '');
        if (str.includes("\n") === true) {
            let parts = str.split("\n");
            if (parts.length > 1) {
                parts.shift();
                let lines = [];
                let count = 0;
                for (const meaning of parts) {
                    if (count == 0) {
                        lines.push(<Fragment>{meaning}<br/><br/></Fragment>);
                    } else {
                        lines.push(<Fragment>{meaning}<br/></Fragment>);
                    }
                    count = count + 1;
                }
                return (<Fragment>{wordType} {lines}</Fragment>);
            } else {
                return (<Fragment>{wordType} {str}</Fragment>);
            }
        } else {
            let parts = str.split(",");
            if (parts.length > 1) {
                parts.shift();
                parts = parts.join("").split(";");
                let lines = [];
                for (const meaning of parts) {
                    lines.push(<Fragment>{meaning}<br/></Fragment>);
                }
                return (<Fragment>{wordType}<br/>{lines}</Fragment>);
            } else {
                return (<Fragment>{wordType}, {str}</Fragment>);
            }
        }
    }

    function onClickShowOtherSide() {
      let showB = !toggle;
      show(showB);
      setToggle(showB);
    }

    /*
            {data["N1"] && !data['N2'] && !data['N3']? <Button className="ml-2" color="primary" size="sm">N1</Button> : <></>}
            {data["N2"] && !data['N3'] ? <Button className="ml-2" color="warning" size="sm">N2</Button> : <></>}
            {data["N3"]? <Button className="ml-2" color="success" size="sm">N3</Button> : <></>}
     */

    return (
        <Card body inverse color={color} className={classNames({'bg-dark':color === 'light'})} style={{cursor:"pointer"}}>
            <CardBody onClick={() => onClickShowOtherSide()}>
                <CardTitle>
                    <div style={{color:"#4c535e", fontSize:"1rem"}}>{index}.</div>
                </CardTitle>
                <CardTitle>
                    {sideA? sideA : <Fragment></Fragment>}
                </CardTitle>
                <CardText>
                    {toggle? sideB : <Fragment></Fragment>}
                </CardText>
            </CardBody>
            <Button color="info" size="sm" onClick={() => setFuriToggle(!furiToggle)}>furi</Button>
        </Card>
    )
}


const FlashcardJPApp = (props) => {
  const { location, json_data } = props

  const [data, setData] = useState("");
  const [cards, setCards] = useState([]);
  const [lines, setLines] = useState([]);

  const [flipToggle, setFlipToggle] = useState(false);
  const [furiToggle, setFuriToggle] = useState(false);

  const [curVocabType, setCurVocabType] = useState("");

  const [n1Toggle, setN1Toggle] = useState(false);
  const [n2Toggle, setN2Toggle] = useState(false);
  const [n3Toggle, setN3Toggle] = useState(false);
  const [nAllToggle, setNAllToggle] = useState(false);


  useEffect(() => {

      setData("");

      setFlipToggle(false);
      setFuriToggle(false);

      async function initData() {
          //const response = await MyAPI.getData(someId);
          const local_data = await getLocalData();
          parseData(local_data);
      }

      initData();
      /*
      if (!json_data) {
          initData();
      } else {
          parseData(json_data);
      }
       */

      /*
      let local_data = false;
      if (!json_data) {
          local_data = async () => {await getLocalData(json_data)};
          parseData(local_data);
      } else {
          parseData(json_data);
      }
       */

  }, [location.pathname])


  function onClickN1(showFlip, showFuri) {
    let cards = [];
    let vocabs = data["n1_vocabs"];
    let count = 0;
    for (const key of Object.keys(vocabs)) {
        count = count + 1;
        let word = vocabs[key];
        cards.push(<Flashcard key={"n1_"+count} index={count} color={'light'} showFuri={showFuri} flip={showFlip} data={word} />);
    }
    setCards(cards);
  }

  function onClickN2(showFlip, showFuri) {
    let cards = [];
    let vocabs = data["n2_vocabs"];
    let count = 0;
    for (const key of Object.keys(vocabs)) {
        count = count + 1;
        let word = vocabs[key];
        cards.push(<Flashcard key={"n2_"+count} index={count} color={'light'} showFuri={showFuri} flip={showFlip} data={word} />);
    }
    setCards(cards);
  }

  function onClickN3(showFlip, showFuri) {
    let cards = [];
    let vocabs = data["n3_vocabs"];
    let count = 0;
    for (const key of Object.keys(vocabs)) {
        count = count + 1;
        let word = vocabs[key];
        cards.push(<Flashcard key={"n3_"+count} index={count} color={'light'} showFuri={showFuri} flip={showFlip} data={word} />);
    }
    setCards(cards);
  }

  function onClickAdverbs(showFlip, showFuri) {
    let lines = data["content"];
    let vocabs = {};
    for (const line_items of lines) {
        for (const word of line_items) {
            if (word["pos1"] === "副詞") {
                vocabs[word["orthBase"]] = word;
            }
        }
    }

    let cards = [];
    let count = 0;
    for (const key of Object.keys(vocabs)) {
        let word = vocabs[key];
        count = count + 1;
        cards.push(<Flashcard key={"adv_"+count} index={count} color={'light'} showFuri={showFuri} flip={showFlip} data={word} />);
    }
    setCards(cards);
  }

  function onClickNouns(showFlip, showFuri) {
    let lines = data["content"];
    let vocabs = {};
    for (const line_items of lines) {
        for (const word of line_items) {
            if (word["pos1"] === "名詞") {
                vocabs[word["orthBase"]] = word;
            }
        }
    }

    let cards = [];
    let count = 0;
    for (const key of Object.keys(vocabs)) {
        let word = vocabs[key];
        count = count + 1;
        cards.push(<Flashcard key={"noun_"+count} index={count} color={'light'} showFuri={showFuri} flip={showFlip} data={word} />);
    }
    setCards(cards);
  }

  function onClickVerbs(showFlip, showFuri) {
    let lines = data["content"];
    let vocabs = {};
    for (const line_items of lines) {
        for (const word of line_items) {
            if (word["pos1"] === "動詞") {
                vocabs[word["orthBase"]] = word;
            }
        }
    }

    let cards = [];
    let count = 0;
    for (const key of Object.keys(vocabs)) {
        let word = vocabs[key];
        count = count + 1;
        cards.push(<Flashcard key={"v_"+count} index={count} color={'light'} showFuri={showFuri} flip={showFlip} data={word} />);
    }
    setCards(cards);
  }

  function onClickAdjectives(showFlip, showFuri) {
    let lines = data["content"];
    let vocabs = {};
    for (const line_items of lines) {
        for (const word of line_items) {
            if ((word["cType"] === "形容詞") || (word["pos1"] === "形状詞")) {
                vocabs[word["orthBase"]] = word;
            }
        }
    }
    let cards = [];
    let count = 0;
    for (const key of Object.keys(vocabs)) {
        let word = vocabs[key];
        count = count + 1;
        cards.push(<Flashcard key={"adj_"+count} index={count} color={'light'} showFuri={showFuri} flip={showFlip} data={word} />);
    }
    setCards(cards);
  }

  function displayVocabType(vocabType, showFlip, showFuri) {
      if (vocabType === "adjectives") {
          onClickAdjectives(showFlip, showFuri);
      } else if (vocabType === "nouns") {
          onClickNouns(showFlip, showFuri);
      } else if (vocabType === "verbs") {
          onClickVerbs(showFlip, showFuri);
      } else if (vocabType === "adverbs") {
          onClickAdverbs(showFlip, showFuri);
      } else if (vocabType === "n1") {
          onClickN1(showFlip, showFuri)
      } else if (vocabType === "n2") {
          onClickN2(showFlip, showFuri)
      } else if (vocabType === "n3") {
          onClickN3(showFlip, showFuri)
      }
  }

  function onSetVocabType(vocabType) {
      setCurVocabType(vocabType);
      displayVocabType(vocabType, flipToggle, furiToggle);
  }

  function onClickToggleFlip() {
      let toggle = !flipToggle;
      displayVocabType(curVocabType, toggle, furiToggle);
      setFlipToggle(toggle);
  }

  function onClickToggleFurigana() {
    let toggle = !furiToggle;
    displayVocabType(curVocabType, flipToggle, toggle);
    setFuriToggle(toggle);
  }

  const getLocalData = async () => {
    //let path = "/data/jp/" + "content_50_verbs" + ".json";
    let path = "/data/jp/" + "content" + ".json";
    let json_data = await fetch(path,
        {
            headers : {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        }).then(function(response) {
            console.log(response);
            return response.json();
        }).catch(err => {
          console.log(err);
          return false;
        });

    return json_data;
  }

  const parseData = (json_data) => {
    if (!json_data) {
        return false;
    }

    let lines = json_data["content"];
    let count = 0;
    let tmp_lines = [];
    for (const line of lines) {
        let line_buffer = [];
        for (const word of line) {
            count = count + 1;
            if ("furi" in word) {
                line_buffer.push("<rb>"+ word["surface"] + "</rb><rt>"+ word["furi"] + "</rt>");
            } else {
                line_buffer.push("<rb>"+ word["surface"] + "</rb><rt></rt>");
            }
        }
        tmp_lines.push("<ruby>" + line_buffer.join("") + "</ruby><br/><br/>")
    }

    let joined_line = tmp_lines.join("");
    setLines([joined_line]);
    setData(json_data);
  }

  //<div dangerouslySetInnerHTML={{__html: "<ruby><rb>これは</rb><rt></rt><rb>日</rb><rt>に</rt><rb>本</rb><rt>ほん</rt><rb>語</rb><rt>ご</rt>です。</ruby>"}} />

  return (
  <Fragment>

    <FalconCardHeader title={"content test"} >
    </FalconCardHeader>

    <Card>
      <CardBody>

          <Nav pills>
              <CustomInput id="input_flip" label="Flip" onChange={() => onClickToggleFlip()} checked={flipToggle} type="checkbox"/>
              <CustomInput className="ml-2" id="input_furi" label="Furi" onChange={() => onClickToggleFurigana()} checked={furiToggle} type="checkbox"/>
              <Button color="primary" className="ml-2" onClick={() => onSetVocabType("n1")} >N1</Button>
              <Button color="primary" className="ml-2" onClick={() => onSetVocabType("n2")} >N2</Button>
              <Button color="primary" className="ml-2" onClick={() => onSetVocabType("n3")} >N3</Button>
              <Button color="primary" className="ml-2" onClick={() => onSetVocabType("nouns")} >Nouns</Button>
              <Button color="primary" className="ml-2" onClick={() => onSetVocabType("adjectives")} >Adjectives</Button>
              <Button color="primary" className="ml-2" onClick={() => onSetVocabType("verbs")} >Verbs</Button>
              <Button color="primary" className="ml-2" onClick={() => onSetVocabType("adverbs")} >Adverbs</Button>
          </Nav>

          <br/>

          <CardColumns>
              {cards ? cards : <Fragment></Fragment>}
          </CardColumns>

      </CardBody>
    </Card>

    {lines ? <Fragment></Fragment> : <Fragment></Fragment>}
    <div className="m-5" dangerouslySetInnerHTML={{__html: lines}} />

  </Fragment>
  );
};

export default FlashcardJPApp;
