import React, { Fragment, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Row, Col, Form, FormGroup, Label, Input, Button, Card, CardBody } from 'reactstrap';
import classNames from 'classnames';
import fetch from "cross-fetch";


const GETClient = (props) => {

  const [response, setResponse] = useState("");
  const [responseData, setResponseData] = useState(false);
  const [payload, setPayload] = useState(false);
  const [isSending, setIsSending] = useState(false);

  const { register, handleSubmit, errors, watch } = useForm();
  const [formData, setData] = useState({});

  const OnGetSubmit = (data, e) => {
      e.preventDefault();
      setData(data);
      setPayload(false);
      setResponseData(false);
  };

  useEffect(() => {
      if (formData && formData.get_endpoint) {
          getData();
      }
  }, [formData]);

  const getData = async () => {
        if (isSending) return;

        setIsSending(true);

        const url = process.env.REACT_APP_TITLE_CLASSIFY_ENDPOINT + "/" + formData["get_endpoint"];
        console.log(url);
        let response = await fetch(url,
            {
                method: 'GET',
                headers: {'Content-Type': 'application/json'},
            }).then(response => {
                if (!response.ok) {
                    throw new Error(response.statusText)
                }
                return response.json();
        }).catch(err => {
            console.log(err);
            return false;
        });

        //console.log(response);
        setResponseData(JSON.stringify(response, null, 2));

        setIsSending(false);
  };

  return (
      <Fragment>
            <Row className="ml-5 mt-5">
                <Col md={7}>
                    <Form onSubmit={handleSubmit(OnGetSubmit)}>
                        <FormGroup>
                            <Label for="get_endpoint">get request end point</Label>
                            <Input
                                type="text"
                                name="get_endpoint"
                                id="get_endpoint"
                                defaultValue={"hello_world"}
                                placeholder="Enter end point"
                                innerRef={register({
                                    required: 'required',
                                })}
                                className={classNames({ 'border-danger': errors.get_endpoint })}
                            />
                            {errors.get_endpoint && <span className="text-danger fs--1">{errors.get_endpoint.message}</span>}
                        </FormGroup>

                        <FormGroup>
                        </FormGroup>

                        <Row>
                            <Col>
                            </Col>
                            <Col>
                            </Col>
                        </Row>

                        <Button type="submit" color="primary">
                            Get
                        </Button>

                    </Form>
                </Col>
                <Col md="auto">
                </Col>
            </Row>
            <Row className="ml-5 mt-3">
                <Col md={7}>
                    <Card>
                      <CardBody>
                          {responseData ? <pre>{responseData}</pre> : <Fragment></Fragment>}
                      </CardBody>
                    </Card>
                </Col>
                <Col md="auto">
                </Col>
            </Row>
      </Fragment>
  )
}


const APITitleClassificationTestClient = (props) => {

  const { location } = props

  const [response, setResponse] = useState("");
  const [responseData, setResponseData] = useState(false);
  const [isSending, setIsSending] = useState(false);

  const { register, handleSubmit, errors, watch } = useForm();
  const [formData, setData] = useState({});

  const [reqData, setReqData] = useState("");

  const OnPostSubmit = (data, e) => {
      setData(data);
      setResponseData(false);
  };

    useEffect(() => {
        if (formData && formData.inputText && formData.inputText !== "") {
            setReqData(formData);
            setTimeout(() => {
                //console.log("Delayed for 1 second.");
                postData();
            }, "500")
        }
    }, [formData]);

    const postData = async () => {
        if (isSending) return;

        setIsSending(true);

        const url = process.env.REACT_APP_TITLE_CLASSIFY_ENDPOINT + "/" + formData["endpoint"];
        console.log(url);

        let input = [formData["inputText"]];
        let body = {instances: JSON.stringify(input)};
        //console.log(formData["endpoint"]);
        //console.log(body);
        let response = await fetch(url,
            {
                method: 'POST',
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify(body)
            }).then(response => {
            if (!response.ok) {
                throw new Error(response.statusText)
            }
            return response.json();
        }).catch(err => {
            console.log(err);
            return false;
        });

        setResponseData(response);
        setData({});

        setIsSending(false);
  };

    // <GETClient />

    return (
        <Fragment>
            <Row className="m-5">
                <Col md={7}>
                    <h1>Hacker News Title Classification Demo</h1>
                    <p>Submit the request below and the query will be served by a trained tensorflow model from a python FastAPI server on Google Cloud</p>
                    <br/>
                    <Form onSubmit={handleSubmit(OnPostSubmit)}>
                        <FormGroup>
                            <Label for="endpoint">post request end point:</Label>
                            <Input
                                type="text"
                                name="endpoint"
                                id="endpoint"
                                defaultValue={"predict"}
                                placeholder="Enter end point"
                                innerRef={register({
                                    required: 'required',
                                })}
                                className={classNames({ 'border-danger': errors.endpoint })}
                            />
                            {errors.endpoint && <span className="text-danger fs--1">{errors.endpoint.message}</span>}
                        </FormGroup>

                        <FormGroup>
                            <Label for="inputText">enter text title to classify:</Label>
                            <Input
                                type="textarea"
                                name="inputText"
                                id="inputText"
                                height="50"
                                placeholder="enter title text"
                                innerRef={register({
                                    required: 'required',
                                    minLength: {
                                        value: 2,
                                        message: 'Minimum 2 character'
                                    }
                                })}
                                className={classNames({ 'border-danger': errors.inputText })}
                            />
                            {errors.inputText && <span className="text-danger fs--1">{errors.inputText.message}</span>}
                        </FormGroup>

                        <FormGroup>
                        </FormGroup>

                        examples: <br/>
                        anaconda   conda on heroku  installing scikit-learn  scipy  and numpy<br/>
                        Lopping, Tips and the Z-List: Bias Lawsuit Explores Harvard’s Admissions<br/>
                        asia gains a tech edge by backing start-ups<br/>
                        tim cook on apple s relationship with facebook   stay tuned <br/>
                        <Row>
                            <Col>
                            </Col>
                            <Col>
                            </Col>
                        </Row>

                        <Button className="mt-2" type="submit" color="primary">
                            Send Prediction Request
                        </Button>
                    </Form>
                </Col>
                <Col md="auto">
                </Col>
            </Row>

            <Row className="m-5">
                <Col md={10}>
                    <Card>
                      <CardBody>
                            {reqData ? <pre>{JSON.stringify(reqData, null, 2)}</pre> : <Fragment></Fragment>}
                      </CardBody>
                    </Card>
                </Col>
            </Row>

            <Row className="m-5 mb-10">
                <Col md={7}>
                    <Card>
                      <CardBody>
                          {isSending ? <Fragment>Waiting for response, Google Run container might take some time to start up...</Fragment> : <Fragment></Fragment>}
                          {responseData ? <pre>{JSON.stringify(responseData, null, 2)}</pre> : <Fragment></Fragment>}
                      </CardBody>
                    </Card>
                </Col>
                <Col md="auto">
                </Col>
            </Row>

        </Fragment>
  );
};

export default APITitleClassificationTestClient;
