import React, { Fragment, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Row, Col, Form, FormGroup, Label, Input, Button, Collapse, Card, CardBody, CardColumns, CardTitle, CardText, Nav, NavItem, NavLink } from 'reactstrap';
import classNames from 'classnames';
import FlashcardJPApp from "../csflashcard/FlashcardJPApp";

import fetch from "cross-fetch";


const APITestClient = (props) => {

  const { location } = props

  const [response, setResponse] = useState("");
  const [responseData, setResponseData] = useState(false);
  const [payload, setPayload] = useState(false);
  const [isSending, setIsSending] = useState(false);

  const { register, handleSubmit, errors, watch } = useForm();
  const [formData, setData] = useState({});

  const OnSubmit = (data, e) => {
      setData(data);
      setPayload(false);
      setResponseData(false);
  };

    useEffect(() => {
    }, []);

    const fetchData = async () => {
        if (isSending) return;

        setIsSending(true);

        const url = process.env.REACT_APP_GCLOUD_ENDPOINT + "/" + formData["endpoint"];
        let parts = formData["inputText"].split("\n");
        let body = {content: parts};
        //headers: new Headers({'Content-Type': 'application/json'}),
        console.log(JSON.stringify(body));
        let response = await fetch(url,
            {
                method: 'POST',
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify(body)
            }).then(response => {
            if (!response.ok) {
                throw new Error(response.statusText)
            }
            return response.json();
        }).catch(err => {
            console.log(err);
            return false;
        });

        setPayload(response["payload"]);
        setResponseData(JSON.stringify(response));

        setIsSending(false);
  };

    return (
        <Fragment>
            <Row className="m-5">
                <Col md={7}>
                    <Form onSubmit={handleSubmit(OnSubmit)}>
                        <FormGroup>
                            <Label for="endpoint">End Point</Label>
                            <Input
                                type="text"
                                name="endpoint"
                                id="endpoint"
                                defaultValue={"jp/convert"}
                                placeholder="Enter end point"
                                innerRef={register({
                                    required: 'required',
                                })}
                                className={classNames({ 'border-danger': errors.endpoint })}
                            />
                            {errors.endpoint && <span className="text-danger fs--1">{errors.endpoint.message}</span>}
                        </FormGroup>

                        <FormGroup>
                            <Label for="inputText">Enter Text</Label>
                            <Input
                                type="textarea"
                                name="inputText"
                                id="inputText"
                                height="50"
                                placeholder="Enter text"
                                innerRef={register({
                                    required: 'required',
                                    minLength: {
                                        value: 2,
                                        message: 'Minimum 2 character'
                                    }
                                })}
                                className={classNames({ 'border-danger': errors.inputText })}
                            />
                            {errors.inputText && <span className="text-danger fs--1">{errors.inputText.message}</span>}
                        </FormGroup>

                        <FormGroup>
                        </FormGroup>

                        <Row>
                            <Col>
                            </Col>
                            <Col>
                            </Col>
                        </Row>

                        <Button type="submit" color="primary">
                            Submit
                        </Button>
                    </Form>
                </Col>
                <Col md="auto">
                    <h5 className="mt-4">Input</h5>
                    <pre>{JSON.stringify(formData, null, 2)}</pre>
                </Col>
            </Row>
            <Row className="m-5">
                <Col md={7}>
                    <Card>
                    <CardBody>
                        <Nav pills>
                            <Button color="primary" className="ml-2" disabled={isSending} onClick={()=>{fetchData()}}>Test Get</Button>
                        </Nav>

                        <p></p>

                        <CardColumns>
                            {responseData ? responseData : <Fragment></Fragment>}
                        </CardColumns>

                    </CardBody>
                </Card>
                </Col>
                <Col md="auto">
                </Col>
            </Row>
            <Row className="m-5">
                <Col md={12}>
                    {payload? <FlashcardJPApp key={"output"} {...props} json_data={payload} /> : <></>}
                </Col>
                <Col>
                </Col>
            </Row>
        </Fragment>
  );
};

export default APITestClient;
