const ChartComponent = props => {
	const {
		data,
		colors: {
			backgroundColor = 'white',
			lineColor = '#2962FF',
			textColor = 'black',
			areaTopColor = '#2962FF',
			areaBottomColor = 'rgba(41, 98, 255, 0.28)',
		} = {},
	} = props;

    const [jsonData, setJsonData] = useState([]);
    const [dataLoaded, setDataLoaded] = useState(true);

    const [volumeData, setVolumeData] = useState([]);
    //const [volumeLoaded, setVolumeLoaded] = useState(false);

	const chartContainerRef = useRef();

    const fetchPrices = () => {
      return fetch("http://127.0.0.1:8000/data")
          .then((response) => response.json())
          .then((data) => {return data});
    }

    const fetchVolume = () => {
      return fetch("http://127.0.0.1:8000/data_volume")
          .then((response) => response.json())
          .then((data) => {return data});
    }

    const fetchData = useCallback(() => {
      const fetchingData = async () => {
        const [prices, volume] = await Promise.all([
            fetchPrices(),
            //fetchVolume()
        ])
        setJsonData(prices);
        //setVolumeData(volume)
        setDataLoaded(true);
      }
      fetchingData()
    }, []);

  useEffect(
      () => {

        if (dataLoaded == false) {
          fetchData();
        }

        // Get the current users primary locale
        const currentLocale = window.navigator.languages[0];
        // Create a number format using Intl.NumberFormat
        const myPriceFormatter = Intl.NumberFormat(currentLocale, {
          style: 'currency',
          currency: 'USD', // Currency for data points
        }).format;

        const handleResize = () => {
          chart.applyOptions({
            width: chartContainerRef.current.clientWidth,
            height: chartContainerRef.current.clientHeight,
          });
        };

        const chart = createChart(chartContainerRef.current, {
          layout: {
            fontFamily: "'Roboto', sans-serif",
            //background: { type: ColorType.Solid, color: backgroundColor },
            background: { type: ColorType.VerticalGradient, color: '#222' },
            textColor: '#DDD',
          },
          grid: {
            vertLines: { color: '#141414' },
            horzLines: { color: '#141414' },
          },
          //width: chartContainerRef.current.clientWidth,
          width: 1000,
          height: 600,
          rightPriceScale: {
            visible: true,
          },
          leftPriceScale: {
            visible: true,
          },
          localization: {
            //priceFormatter: myPriceFormatter,
            dateFormat: "yyyy-MM-dd",
          },
        });

        // Setting the border color for the vertical axis
        chart.priceScale().applyOptions({
          borderColor: '#71649C',
        });

        // Setting the border color for the horizontal axis
        chart.timeScale().applyOptions({
          borderColor: '#71649C',
        });

        chart.timeScale().fitContent();

        //const newSeries = chart.addAreaSeries({ lineColor, topColor: areaTopColor, bottomColor: areaBottomColor });
        //newSeries.setData(data);

        /*
        // Generate sample data to use within a candlestick series
        const candleStickData = generateCandlestickData().map((datapoint) => {
            // map function is changing the color for the individual
            // candlestick points that close above 205
            if (datapoint.close < 205) return datapoint;
            // we are adding 'color' and 'wickColor' properties to the datapoint.
            // Using spread syntax: https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Spread_syntax#spread_in_object_literals
            return { ...datapoint, color: "orange", wickColor: "orange" };
        });
         */

        if (dataLoaded == true) {

          //console.log(jsonData);

          /*
          const candleStickData2 = jsonData.map((datapoint) => {
            return datapoint;
          });
           */

          const candleStickData2 = generateCandlestickData().map((datapoint) => {
            return datapoint;
          });

          const abv200Series = chart.addLineSeries({
            color: 'blue',
            lineWidth:0.8,
            lastValueVisible: true,
            priceLineVisible: false,
            crosshairMarkerVisible: false,
            priceScaleId: 'left',
          });
          const abv200Data = generateCandlestickData().map((datapoint, idx, array) => {
            return { time:datapoint.time, value: datapoint.abv_200_count};
          });
          abv200Series.setData(abv200Data);

          const ma20Series = chart.addLineSeries({
            color: 'gold',
            lineWidth:0.8,
            lastValueVisible: true,
            priceLineVisible: false,
            crosshairMarkerVisible: false,
            //priceScaleId: 'left',
          });
          const ma20Data = generateCandlestickData().map((datapoint, idx, array) => {
            return { time:datapoint.time, value: datapoint.ma_20};
          });
          ma20Series.setData(ma20Data);

          const ma60Series = chart.addLineSeries({
            color:'red',
            lineWidth:0.8,
            lastValueVisible: true,
            priceLineVisible: false,
            crosshairMarkerVisible: false,
          });
          const ma60Data = generateCandlestickData().map((datapoint, idx, array) => {
            return { time:datapoint.time, value: datapoint.ma_60};
          });
          ma60Series.setData(ma60Data);

          const ma200Series = chart.addLineSeries({
            color:'green',
            lineWidth:0.8,
            lastValueVisible: true,
            priceLineVisible: false,
            crosshairMarkerVisible: false,
          });
          const ma200Data = generateCandlestickData().map((datapoint, idx, array) => {
            return { time:datapoint.time, value: datapoint.ma_200};
          });
          ma200Series.setData(ma200Data);

          // Create the Main Series (Candlesticks)
          const mainSeries = chart.addCandlestickSeries({
            lastValueVisible: true,
            priceLineVisible: false,
          });
          // Set the data for the Main Series
          //mainSeries.setData(candleStickData);
          mainSeries.setData(candleStickData2);

          /*
          // Changing the Candlestick colors
          mainSeries.applyOptions({
            wickUpColor: "rgb(54, 116, 217)",
            upColor: "rgb(54, 116, 217)",
            wickDownColor: "rgb(225, 50, 85)",
            downColor: "rgb(225, 50, 85)",
            borderVisible: false,
          });
           */

          // Adjust the options for the priceScale of the mainSeries
          mainSeries.priceScale().applyOptions({
            autoScale: false, // disables auto scaling based on visible content
            scaleMargins: {
              top: 0.1,
              bottom: 0.2,
            },
          });

          const volumeData2 = generateCandlestickData().map((datapoint, idx, array) => {
            //let color = "#ef5350";
            let color = "#804f4e";
            if (idx > 0) {
              if (candleStickData2[idx].close < candleStickData2[idx-1].close) {
                //color = "#26a69a";
                color = "#20736b";
              }
            }
            return { time:datapoint.time, value: datapoint.amount, color: color};
          });

          const volumeSeries = chart.addHistogramSeries({
            //color: '#26a69a',
            priceFormat: {
              type: 'volume',
            },
            priceScaleId: '', // set as an overlay by setting a blank priceScaleId
          });

          volumeSeries.setData(volumeData2);

          volumeSeries.priceScale().applyOptions({
            scaleMargins: {
              top: 0.7, // highest point of the series will be 70% away from the top
              bottom: 0,
            },
          });

          chart.timeScale().fitContent();
        }

        window.addEventListener('resize', handleResize);

        return () => {
          window.removeEventListener('resize', handleResize);
          chart.remove();
        };
      },
      [data, backgroundColor, lineColor, textColor, areaTopColor, areaBottomColor, fetchData, dataLoaded]
  );

	return (
		<div
			ref={chartContainerRef}
		/>
	);
};

